import React, { useState, useEffect, useRef } from 'react'
import './DrawGameView.css';
import axios from 'axios';

// helpers
import paletteHelpers from '../../helpers/paletteHelpers';
import drawHelpers from '../../helpers/drawHelpers';
import constants from '../../constants';

// Notes: We will remove the NavButton component later and replace it with a socket emit so it automatically moves to the next page for all the players

// subcomponents
import NavButton from '../NavButton';
import Palette from '../Palette';
import MainImage from '../MainImage';
import LineSize from '../LineSize';
import CountdownTimer from '../CountdownTimer';

// styles
// import IMG_SRC from '../../assets/mona-lisa.jpg'
import IMG_SRC from '../../assets/alternate2.jpg'
import DrawGameViewStyles from './DrawGameViewStyles';
import RoundFinishedBlocker from '../RoundFinishedBlocker';



const { API, ROUND_TIME, CANVAS_W, CANVAS_H } = constants;
const { silhouetteStyles, slicerStyles } = DrawGameViewStyles;


export default function DrawGameView(props) {
  const { setGamePromptHandler, lobbyID, socket, changeViewHandler, myQuadrant, myLobbyObj, setFinalCoordinatesHandler } = props;

  const [state, setState] = useState({
    coordinates: [],
    drawing: false,
    currentColor: 'black',
    currentLineSize: 6,
    openLineSize: false,
    openColor: false,
    roundFinished:false,
    maxWidth: null,
    maxHeight: null,
    opacity: 1,
    undoLengths: [],
    currentUndoLength: 0
  });

  const { coordinates, drawing, currentColor, currentLineSize, openLineSize, openColor, roundTime, roundFinished, maxWidth, maxHeight, opacity, undoLengths, currentUndoLength } = state;


  // helpers
  const { togglePalette, toggleLineSize, updateLineSize, updateColor } = paletteHelpers;
  const { onMouseOutHandler, onMouseUpHandler, onMouseDownHandler, onMouseMoveHandler, draw, clearCanvas, undoCanvas } = drawHelpers;

  /** Game state listeners **/
  useEffect(() => {
    socket.on('roundFinished', () => {
      // console.log('Round finished!');
      const roundFinished = true;
      
      setTimeout(() => {
        changeViewHandler('ResultsView');
      }, 2000);

      setState(prev => ({...prev, roundFinished}));
    })
    
    /** Listen for final coordinates at the end of the game **/
    socket.on('finalCoordinates', finalCoordinates => {

      // console.log('Updating final coordinates.....');
      setFinalCoordinatesHandler(finalCoordinates);
      socket.emit('disconnectClient', lobbyID);
    })

    socket.on('fadeSilhouette', opacity => {
      setState(prev => ({...prev, opacity}));
    })

    socket.on('err', errMsg => {
      setGamePromptHandler(errMsg);
    })
  }, [])


  // send final coordinates before view change
  useEffect(() => {
    if(roundFinished) {
      const PLAYERS_IN_ROOM = Object.keys(myLobbyObj.players).length;
      
      const data = {
        coordinates,
        lobbyID,
        myQuadrant,
        PLAYERS_IN_ROOM
      }

      // console.log('Game finished. Sending final coordinates...', coordinates);

      axios.post(API + '/finalCoords', data)
      .then(() => {
        // set mouse pointer events in this view to none
      })
      .catch(err => console.log(err));
    }
  }, [roundFinished])


  // canvas
  const canvasRef = useRef(null);
  useEffect(() => {
    const canvas = canvasRef.current;
    const ctx = canvas.getContext('2d');
    ctx.clearRect(0, 0, window.innerHeight, window.innerWidth);
    coordinates.forEach(coordinate => {
      const { x, y, color, lineSize } = coordinate;
      const data = {
        x: x * maxWidth,
        y: y * maxHeight,
        color,
        lineSize
      }
      draw(ctx, data);
    });
  });

  /** STRETCH - Undo logic **/
  useEffect(() => {
    if(!drawing) {
      setState(prev => ({...prev, undoLengths: [...undoLengths, currentUndoLength], currentUndoLength: 0}));
    }
  }, [drawing]);


  // ** BG LOGIC AND TRANSFORMATIONS ** //
  const _silhouetteStyles = silhouetteStyles();
  const _slicerStyles = slicerStyles();
  const bg_h = (window.innerHeight * 0.9) * 2;
  const bg_w = bg_h * 0.8;
  
  
  let top;
  let right;
  if(myQuadrant === 'quadrant_1') {
    top = '50%';
    right = '50%';
  } else if(myQuadrant === 'quadrant_2') {
    top = '50%';
    right = '150%';
  } else if(myQuadrant === 'quadrant_3') {
    top = '-50%';
    right = '50%';
  } else if(myQuadrant === 'quadrant_4') {
    top = '-50%';
    right = '150%';
  }
  _silhouetteStyles['top'] = top;
  _silhouetteStyles['right'] = right;
  _silhouetteStyles['opacity'] = opacity;

  /** Set slicer height and width **/
  _slicerStyles['height'] = CANVAS_H;
  _slicerStyles['width'] = CANVAS_W;


  // ** PALETTE BUTTONS LOGIC ** //
  const lineSizeClickHandler = e => {
    setState(prev => ({...prev, openColor: false}));
    toggleLineSize(e, state, setState);
  }

  const colorClickHandler = e => {
    setState(prev => ({...prev, openLineSize: false}));
    togglePalette(e, state, setState);
  }


  return (
    <div>
      <div className="InstructionsView__header App__colorScheme--header" >
        <CountdownTimer
        warningOn={true}
        timeInMS={ROUND_TIME}/>

        <button
        className="App__colorScheme--palette"
        onClick={e => colorClickHandler(e)}><i
        style={{color: currentColor}} className="fas fa-palette"></i>
        </button>

        <button
          className="App__colorScheme--palette"
          onClick={e => lineSizeClickHandler(e)}><i 
          style={{color: 'black'}}
          className="fas fa-paint-brush"></i>
        </button>

        <button 
        style={{padding:'0 10px'}}
        className="App__colorScheme--palette" onClick={e => undoCanvas(e, state, setState)}>Undo</button>
        
        <button 
        style={{padding:'0 10px'}}
        className="App__colorScheme--palette" onClick={e => clearCanvas(e, state, setState)}>Clear</button>

      </div>


      {openColor && (<Palette
        setState={setState}
        state={state}
        updateColor={updateColor}/>)}
      {openLineSize && (<LineSize
        setState={setState}
        state={state}
        updateLineSize={updateLineSize}/>)}


      {/* Begin: Canvas and draw functionality */}
      <div className="DrawGameView--container">
        <div
        className="DrawGameView--slicer"
        style={_slicerStyles}>
          <img
          className="DrawGameView--silhouette"
          style={_silhouetteStyles}
          height={bg_h}
          width={bg_w}
          src={IMG_SRC} alt="silhouette"/>
        </div>
        <canvas
        className="DrawGameView--canvas"
        ref={canvasRef}
        width={CANVAS_W}
        height={CANVAS_H}
        onMouseOut={e => onMouseOutHandler(setState, currentUndoLength)}
        onMouseDown={e => onMouseDownHandler(setState)}
        onMouseUp={e => onMouseUpHandler(setState, currentUndoLength)}
        onMouseMove={e => onMouseMoveHandler(e, state, setState, CANVAS_W, CANVAS_H)}></canvas>
      </div>
      {/* End: Canvas and draw functionality */}

      <RoundFinishedBlocker roundFinished={roundFinished}/>

    </div>
  )
}